import Logo from "../../img/bilaine.svg"
import Dragon from "../../img/dragon.png"
import  "./index.scss"
import {Link} from "react-router-dom";
import React, {useEffect, useState} from "react";
import ScrollToTop from "../../components/utils/ScrollToTop";

const MainPage = () => {
    const [firstText, setFirstText] = useState(true)
    useEffect(()=>{
        document.body.style.overflow='hidden'
    },[])
    ScrollToTop()
    window.addEventListener('resize', () => {
        //    
        let vh = window.innerHeight * 0.01;
        document.documentElement.style.setProperty('--vh', `${vh}px`);
    });
    let maxHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
    return (
        <div className="mainPageContainer" style={{maxHeight: maxHeight}}>
            <div className={'bg'}/>
            <div className="mainText">
             <img src={Logo}/>
                {firstText ?
                <div>
                <div style={{marginBottom: 12}}>       .       ,    .</div>
                <div>    !  ,        ?     !</div>
                </div> : <div>         b2b-.    ,     .  ,          !,   ,           !</div>
                }
            </div>
            <div className="imgDragon">
                <img src={Dragon}/>
                {/*<div className={'img'}/>*/}
                {firstText ?
                    <button onClick={()=>setFirstText(false)}>
                         !
                    </button>
                    :
                    <Link to={'/game/1'}>
                        <button>!</button>
                    </Link>
                }
            </div>
        </div>
    );
};

export default MainPage;
